import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { OnlyLoggedInUsers } from '../../guards/only-logged-in-users.guard';

const routes: Routes = [
  {
    path: 'main',
    loadChildren: () => import('./main/main.module').then(m => m.MainModule), canActivate: [OnlyLoggedInUsers]
  },
  {
    path: 'sub',
    loadChildren: () => import('./sub/sub.module').then(m => m.SubModule), canActivate: [OnlyLoggedInUsers]
  },
  {
    path: 'child',
    loadChildren: () => import('./child/child.module').then(m => m.ChildModule), canActivate: [OnlyLoggedInUsers]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CategoriesRoutingModule {
}
